/**
** Style single product
*/

.single-product.woocommerce {
	#main {
		padding: 0 0 70px;
	}

	.type-product {
		.woocommerce-product-gallery {
			padding-right: 20px;
			.woocommerce-product-gallery__image {
				padding: 0 !important;
			}
		}
		.entry-summary {
			.product_title {
				font-style: normal;
				font-weight: 500;
				font-size: 36px;
				line-height: 42px;
				display: flex;
				align-items: center;
				text-transform: uppercase;
				font-family: var(--content-font);
				color: #019490;
			}
			.price {
				font-style: normal;
				font-weight: 700;
				font-size: 75px;
				line-height: 69px;
				display: flex;
				align-items: center;
				letter-spacing: -0.02em;
				text-transform: uppercase;
				color: #019490;
			}
			.quote-btn {
				.btn {
					.single_add_to_cart_button.button {
						font-style: normal;
						font-weight: 400;
						font-size: 15px;
						line-height: 18px; 
						display: flex;
						align-items: center;
						text-align: center;
						text-transform: uppercase;
						font-family: var(--content-font);
						color: #ffffff;
						background-position: right center;
						background-repeat: no-repeat;
						background-size: contain;
						background-color: transparent !important;
						background-image: url('../../images/Button-deal.png');
					}
				}
			}
			.single_variation_wrap {
				.single_add_to_cart_button {
					height: 75px;
					font-style: normal;
					font-weight: 400 !important;
					font-size: 18px;
					font-family: var(--content-font);
					line-height: 21px;
					display: flex;
					align-items: center;
					text-align: center;
					text-transform: uppercase;
					color: #ffffff !important;
					width: 310px;
					background-position: right center;
					background-repeat: no-repeat;
					background-size: contain;
					padding-right: 0 0 0 60px  !important;
					background-color: transparent !important;
					background-image: url("data:image/png;base64,") !important;
				}
			}
		}
	}
}
