footer {
	position: relative;
	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 61px;
		background: linear-gradient(180deg, rgba(66, 71, 73, 0.7) 0%, rgba(66, 71, 73, 0) 84.37%);
		mix-blend-mode: multiply;
		z-index: 1;
	}

	.css-vector-footer-bottom{
		@media (min-width: 768px){
			display: none;
		}
		content: "";
		position: absolute;
		bottom: 0;
		right: 0;
		width: 61px;
		height: 34px;
		background-image: url('data:image/png;base64,');
		background-position: center;
		background-repeat: no-repeat;
		background-size: contain;
		z-index: 1;
	}

	.css-vector-footer-top{
		@media (min-width: 768px){
			display: none;
		}
		content: "";
		position: absolute;
		top: 0;
		left: 5px;
		width: 105px;
		height: 51px;
		background-image: url('data:image/png;base64,');
		background-position: center;
		background-repeat: no-repeat;
		background-size: contain;
		z-index: 1;
	}
}
